import Vue from "vue";
import App from "./App.vue";
import store from "./store";

String.prototype.trunc = function (this: string, n: number) {
  let end = this.length;
  if (this.length > n) {
    for (let i = n; i > 0; i--) {
      if (/[^а-яА-Я\w]+$/u.test(this[i])) {
        end = i;
        break;
      }
    }

    let s = this.substr(0, end);

    s = s.replace(/[^а-яА-Я\w]+$/u, "")

    return s + "\u2026";
  }

  return this;
};

Vue.config.productionTip = false;

new Vue({
  store,
  render: h => h(App)
}).$mount('#app');
