



































import Vue from "vue";
import {IPost} from "@/store/types";
import {PropType} from "vue/types/options";

export default Vue.extend({
  name: "ListItem",
  props: {
    post: Object as PropType<IPost>
  },
  data: () => ({
    collapsed: true,
  }),
  methods: {
    toggleCollapse() {
      this.$data.collapsed = !this.$data.collapsed;
    }
  },
  computed: {
    date() {
      const date = new Date(this.post.timestamp);
      const day = date.getDay().toString().padStart(2, "0");
      const hours = date.getHours().toString().padStart(2, "0");
      const minutes = date.getMinutes().toString().padStart(2, "0");
      const monthNum = date.getMonth();


      let month = "";
      switch (monthNum) {
        case 0:
          month = "января";
          break;
        case 1:
          month = "февраля";
          break;
        case 2:
          month = "марта";
          break;
        case 3:
          month = "апреля";
          break;
        case 4:
          month = "мая";
          break;
        case 5:
          month = "июня";
          break;
        case 6:
          month = "июля";
          break;
        case 7:
          month = "августа";
          break;
        case 8:
          month = "сентября";
          break;
        case 9:
          month = "октября";
          break;
        case 10:
          month = "ноября";
          break;
        case 11:
          month = "декабря";
          break;
      }

      return `${day} ${month}, ${hours}:${minutes}`
    }
  }
})
